import React from "react"
import Grid from "@material-ui/core/Grid"
import { makeStyles } from "@material-ui/core/styles";
import { Typography } from "@material-ui/core";


const useStyles = makeStyles(theme => ({
    image:{
        width: "100%",
        height: "100%",
        borderRadius: "50%",
        alignSelf: "center",
    },
    text:{
        width: "100%",
        textAlign: "center",
    }
}));

export default function HeroePanelLeft(props){
    const classes = useStyles();
    const image_url = `http://localhost:5000/image/${props.data.hero_id}.png`
    return(
        <>
        <Grid container spacing={2}>
            <Grid item xs={10} className={classes.text}>
                <Typography variant="h6">
                    K/D/A/ {props.data.kills}:{props.data.deaths}:{props.data.assists}
                </Typography>
                <Typography>
                    GPM:{props.data.gold_per_min} EXPM:{props.data.xp_per_min}
                </Typography>
            </Grid>
            <Grid item xs={2}>
                <img className={classes.image}
                src={image_url} />
            </Grid>
        </Grid>
        </>
    )
}
