import React  from 'react';
import { ThemeProvider } from "@material-ui/core/styles";
import { CssBaseline } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import components from './themes';


//"https://es.dotabuff.com/matches/" + props.text match id
export default function GameLink(props){
    const classes = components.useStyles();
    return(
        <>
        <ThemeProvider theme={components.themes}>
            <Link underline="none" target="_blank" 
            href={"https://es.dotabuff.com/matches/" + props.match_id}>
                <CssBaseline />
                <Typography  align="center" className={classes.pacifico} variant="h2" >
                    Game Link
                </Typography>
            </Link>
        </ThemeProvider>
        </>
    )
}