import Pacificowoff2 from './Fonts/Pacifico-Regular.woff2'
import Pacificottf from './Fonts/Pacifico-Regular.ttf';
import Pacificowoff from './Fonts/Pacifico-Regular.woff';
import Tourneyttf from './Fonts/Tourney-Light.ttf'
import Tourneywoff from './Fonts/Tourney-Light.woff'
import Tourneywoff2 from './Fonts/Tourney-Light.woff2'
import VT323ttf from './Fonts/VT323-Regular.ttf'
import VT323woff from './Fonts/VT323-Regular.woff'
import VT323woff2 from './Fonts/VT323-Regular.woff2'
import { makeStyles } from '@material-ui/core/styles';

import { createMuiTheme } from '@material-ui/core/styles';


const pacifico = {
    fontFamily: 'Pacifico',
        src: `
        local('Pacifico'),
        local('Pacifico-Regular'),
        url(${Pacificottf}) format('truetype'),
        url(${Pacificowoff}) format('woff'),
        url(${Pacificowoff2}) format('woff2')
        `
};

const tourney = {
    fontFamily : 'Tourney',
        src: `
        local('Tourney').
        local('Tourney-Light'),
        url(${Tourneyttf}) format('truetype'),
        url(${Tourneywoff}) format('woff'),
        url(${Tourneywoff2}) format('woff2')
        `
};
const VT323 = {
    fontFamily: 'VT323',
        src: `
        local('VT323'),
        local('VT323-Regular'),
        url(${VT323ttf}) format('truetype'),
        url(${VT323woff}) format('woff'),
        url(${VT323woff2}) format('woff2'),
        `
};

const themes = createMuiTheme({
    typography : {
        fontFamily: [
            '"Tourney"',
            '"VT323"',
            '"Pacifico"',
            'monospace',
            'cursive'
        ].join(',')
    },
    overrides: {
        MuiCssBaseline :{
            '@global': {
                '@font-face': [VT323, pacifico, tourney, ],
            }
        }
    },
})

const useStyles = makeStyles({
    pacifico: {
        fontFamily: "'Pacifico', cursive",
    },
    vt323: {
        fontFamily: "'VT323', monospace",
    },
    tourney:{
        fontFamily: "'Tourney', cursive"
    },
    normal : {
        fontFamily : 'cursive',  
    }
})

export default {themes, useStyles};