import React, {useEffect, useState} from 'react';
import { ThemeProvider } from '@material-ui/core/styles';
import { CssBaseline } from '@material-ui/core';
import components from './themes';
import Typography from '@material-ui/core/Typography';

export default function Victory(props){
    const [victory_message, setMessage] = useState("") ;

    const classes = components.useStyles();
    useEffect(() => {
        if(props.win){
            setMessage("Radiant Victory");
        }else{
            setMessage("Dire Victory");
        }
    }, [victory_message])
    return(
        <>
        <ThemeProvider theme={components.themes}>
            <CssBaseline />
            <Typography align="center" variant="h1" className={classes.tourney}>{victory_message}</Typography>
        </ThemeProvider>
        </>
    )
}