
import React, {useState, useEffect} from 'react'
import {makeStyles} from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'
import Grid from '@material-ui/core/Grid'

const useStyles = makeStyles({
    root: {
        margin: '30px 0px',
        listStyle : 'none',
        alignContent: 'center',
        display : 'flex',
    },
    
})


export default function Pagination(props){
    const [data, setData]               = useState([]);
    const [page, pageNumber]            = useState(0);
    const classes = useStyles();
    useEffect(() => {
        setData(paginationGrid(props.length))
    },[])

    const paginationGrid = (length) => {
        let data = []
        for(let x = 0; x < length ; x++){
            data.push(<Button id={x + 1} value={x + 1} variant="outlined" color="secondary" onClick={Update} disabled={false} >{x + 1}</Button>)
        }
        return data
    }
    const upPage    = (e) => {
        if(page < 15) pageNumber(page + 1)
    }
    const downPage  = (e) => {
        if(page > 0) pageNumber(page - 1)
    }
    const Update = (e) => {
        let number;
        if(e.target.value !== undefined) number = Number(e.target.value);
        else number = Number(e.target.innerText);
        props.setPageNumber(number);
    } 
    return(
        <>
        <Grid container direction="row" justify="center" spacing={3}>
        <Grid item xs={8} className={classes.root} justify="center">
            <Button onClick={downPage} variant="outlined" color="secondary" >&laquo;</Button>
                {data.slice(page, page + 5)}
            <Button onClick={upPage} variant="outlined" color="secondary">&raquo;</Button>
        </Grid>
        </Grid>
        </>
     )
}