import HeroePanelLeft from './HeroePanelLeft';
import HeroePanelRight from './HeroePanelRight';
import Victory from '../title/Victory';
import GameLink from '../title/GameLink';
import Radiant from '../title/Radiant';
import Dire from '../title/Dire';

import React, { useEffect , useState} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Grid from "@material-ui/core/Grid";



const useStyles = makeStyles( () => ({
    root: {
        margin: '30px 0px',
    }
}));

function HeroeGrid(props){
    return (
        <>
        <Grid item xs={5}>
            <HeroePanelRight data={props.data_right}/>
        </Grid>
        <Grid item xs={2} >
           <svg height="100%" width="100%" key={10}>
                <line x1="50%" x2="50%" y1="0%" y2="100%" stroke="red"

                />
           </svg>
        </Grid>
        <Grid item xs={5}>
            <HeroePanelLeft data={props.data_left}/>
        </Grid>
        </>
    )
}

//Function that returns the five panels of the match
export default function HeroesData(props){
    const [data, setData]     = useState([]);
    const [win, setWin]       = useState(true); 
    const [loaded, setLoaded] = useState(false);
    const classes = useStyles();
    const link = "http://127.0.0.1:5000/match_detail?match_id=" + props.match_id;
    useEffect(() => {
        fetch(link)
        .then(res =>  res.json())
        .then((data) => {
            setData(setGrid(data))
            setWin(data.radiant_win)
            setLoaded(true);
        })
        .catch(err => {});
    },[props.match_id])
    if(loaded){
        return(
            <>
            <Grid item xs={12} className={classes.root}>
                {loaded ? <Victory win={win}/> : <div></div>}
            </Grid>
                {data} 
            </>
        )
    }else{

        return(
            <p>Loading</p>
        )
    }
}

const setGrid = (data) => {
    let components = [];
    //Game link push
    components.push(<Grid item xs={12} >
        <GameLink match_id={data.match_id}/>
                    </Grid>);
    //Radiant and dire panel
    components.push(<Grid item xs={6}  >
            <Dire />
    </Grid>)
    components.push(<Grid item xs={6} >
            <Radiant />
    </Grid>)
    for(let x = 0; x < 5 ; x++){
        components.push(<HeroeGrid data_left={data.players[x]} data_right={data.players[x + 5]}/>)
    }
    return components
}