import HeroesData from './components/heroe_panel/HeroePanel';
import Title from './components/title/Title';
import Pagination from './components/pagination/Pagination';

import Grid from '@material-ui/core/Grid';
import React, {useState, useEffect} from 'react';
import {makeStyles} from '@material-ui/core/styles'


const useStyles = makeStyles({
    root: {
        margin: "30px 0px",
    }
})

export default function Model(){
    const classes = useStyles();
    const [data, setData ]          = useState([]);
    const [page, setPage]           = useState(0);
    const [heroPage, setHeroPage]   = useState(<></>)
    const [loaded, setLoaded]       = useState(false);
    useEffect(() => {
        if(!loaded){
            fetch("http://127.0.0.1:5000/match_history")
            .then((res) => {return res.json()})
            .then((data) =>{ 
                setData(data);
                setLoaded(true);
                setHeroPage([<HeroesData match_id={data[page].match_id}/>])
                
            })
            .catch((err) => {
                
            })
        }else{
            const new_hero = <HeroesData match_id={data[page].match_id}/>
            setHeroPage(new_hero);
            
        }
    }, [page])
    return(
      <>
      <Grid container justify="center" className={classes.root}>
          <Grid item xs={7}>
                <Title text={"The last 20 dota games"}/>
          </Grid>
      </Grid>
            {data.length === 0? <p>Loading</p>
            : <Pagination length={data.length} setPageNumber={setPage}/>}
        <Grid container className={classes.root} justify="center">
            {heroPage}
        </Grid>
      </>
    )
}

